
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
  color: red;
}
.popup_inner {
  position: absolute;
  left: 30%;
  right: 30%;
  top: 10%;
  bottom: 30%;
  background: white;
  text-align: center;
  color: red;
}

input.dealy {
  margin-left: 55px;
  color: red;
}

.dealy {
  padding: 0;
  text-align: center;
  color: black;
  display: block;
  color: red;
}


